import React, { useState, Fragment } from "react";
import {
  FormControl,
  FormControlLabel,
  Switch,
  InputLabel,
  OutlinedInput,
  Grid
} from "@material-ui/core";

import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker
} from "@material-ui/pickers";

import DateFnsUtils from "@date-io/date-fns";

function PayChangeData(props) {
  const wageChange = props.wageChange;
  const setWageChange = props.setWageChange;
  const wageChangeDate = props.wageChangeDate;
  const setWageChangeDate = props.setWageChangeDate;
  const moveInDate = props.moveInDate;
  const setMoveInDate = props.setMoveInDate;

  const [payChange, setPayChange] = useState(false);

  function usingPayChange() {
    if (payChange) {
      return (
        <Fragment>
          <Grid item>
            <FormControl fullWidth variant="outlined">
              <InputLabel htmlFor="payRateChange">
                Anticipated Pay Change
              </InputLabel>
              <OutlinedInput
                id="payRateChange"
                label="Anticipated Pay Change"
                type="text"
                value={wageChange}
                onChange={e => setWageChange(e.target.value)}
              />
            </FormControl>
          </Grid>
          <Grid item>
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
              <KeyboardDatePicker
                disableToolbar
                variant="inline"
                format="MM/dd/yyyy"
                margin="normal"
                label="Move In / Recert Date"
                value={moveInDate}
                onChange={e => setMoveInDate(e)}
              />
            </MuiPickersUtilsProvider>
          </Grid>
          <Grid item>
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
              <KeyboardDatePicker
                disableToolbar
                variant="inline"
                format="MM/dd/yyyy"
                margin="normal"
                label="New Pay Rate Effective"
                value={wageChangeDate}
                onChange={e => setWageChangeDate(e)}
              />
            </MuiPickersUtilsProvider>
          </Grid>
        </Fragment>
      );
    }
  }

  return (
    <Fragment>
      <Grid item md={3}>
        <FormControlLabel
          control={
            <Switch
              checked={payChange}
              onChange={e => {
                setPayChange(e.target.checked);
              }}
              name="overTimeSwitch"
              color="secondary"
            />
          }
          label="Anticipated Pay Change"
        />
      </Grid>
      {usingPayChange()}
    </Fragment>
  );
}

export default PayChangeData;
